.loader-container {
	width: 100%;
	height: 100vh;
	display: flex;
	justify-content: space-around;
	align-items: center;
	flex-direction: column;
	position: fixed;
	background: rgba(0, 0, 0, 0.834);
	z-index: 1;
}

.spinner {
	width: 64px;
	height: 64px;
	border: 8px solid;
	border-color: #00425a transparent #00425a transparent;
	border-radius: 50%;
	animation: spin-anim 1.2s linear infinite;
}

.logo-anim {
	width: auto;
	animation: logo-popping 1.2s linear infinite;
}

@keyframes spin-anim {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
}

@keyframes logo-popping {
	0% {
		transform: scale(1);
	}
	50% {
		transform: scale(1.5);
	}
	100% {
		transform: scale(1);
	}
}
